import {useState} from 'react'
import sandwitch from '../../assets/sandwitch.svg'
import close from '../../assets/close.svg'
import DesktopMenu from "./components/desktopMenu";
import MobileMenu from "./components/mobileMenu";

import s from './style.module.css';

function Header({mobileMenu, setMobileMenu}) {
    console.log(mobileMenu)
    console.log(setMobileMenu)
    const [handleClick, setHandleClick] = useState(false)
    return (
        <div className={s.header} id='header'>
            {
                (handleClick ? <MobileMenu/> : <DesktopMenu />)
            }
            <button
                className={s.button}
                style={(handleClick ? {backgroundImage:`url(${close})`} : {backgroundImage:`url(${sandwitch})`})}
                onClick={() => setHandleClick(!handleClick)}
            />
        </div>
    );
}

export default Header;
